#md-indicator {
	display: none;
}

#mobile-indicator {
	display: none;
}

.navbar-trigger {
	display: none;
}

@media (min-width: 768px) {

	.mob-nav-buttons {
		display: none;
	}

}

@media (min-width: 992px) {

	.navbar-wrapper {
		display: block !important;
		opacity: 1 !important;
	}

	.navbar-trigger {
		display: none;
	}

}

// lg

