// md

@media (max-width: 991px) {

	#md-indicator {
		display: block;
	}

	.navbar-wrapper {
		display: none;
	}

	.navbar-trigger {
		display: block;
		width: 30px;
		height: 30px;
		position: relative;
		cursor: pointer;
		margin: 0 auto;
	}

	.navbar-trigger span {
		position: absolute;
		left: 0;
		right: 0;
		height: 2px;
		border-radius: 1px;
		background: #fff;
		transition: all .2s ease;
	}

	.navbar-trigger span:nth-child(1) {
		top: 2px;
	}

	.navbar-trigger span:nth-child(2) {
		top: 50%;
		margin-top: -1px;
	}

	.navbar-trigger span:nth-child(3) {
		bottom: 2px;
	}

	.navbar-trigger.active span:nth-child(1) {
		top: 50%;
		margin-top: -1px;
		transform: rotate(45deg);
	}

	.navbar-trigger.active span:nth-child(2) {
		opacity: 0;
	}

	.navbar-trigger.active span:nth-child(3) {
		bottom: 50%;
		margin-bottom: -1px;
		transform: rotate(-45deg);
	}

	.navbar-wrapper {
		position: fixed;
		left: 0;
		right: 0;
		top: 75px;
		background: #00c7ff;
		box-shadow: 0 0 30px 0 rgba(0,0,0,.15);
		z-index: 100;
		padding: 10px 0;
		margin: 20px 0 0 0;
	}

	.header-fixed .navbar-wrapper {
		margin-top: 0;
	}

	ul.navbar-nav {
		justify-content: center;
	}

	.page-header-home {
		padding-top: 87px;
		margin-bottom: 0;
		padding-bottom: 60px;
	}

	.page-header-home-flamingo {
		margin-left: -680px;
	}

	.page-header-home-flamingo img {
		transform: scale(.5);
		transform-origin: right 0;
	}

	.page-header-home-turtle {
		margin-right: -470px;
	}

	.page-header-home-turtle img {
		transform: scale(.5);
		transform-origin: left 0;
	}

	.page-header-home-duck {
		margin-left: -530px;
	}

	.page-header-home-duck img {
		transform: scale(.5);
		transform-origin: left 0;
	}

	.page-header-home h1, .page-header-home .h1 {
		font-size: 50px;
		max-width: 400px;
	}

	.page-section-home .section-header h2,
	.page-section-home .section-header .h2 {
		font-size: 40px;
	}

	ul.nav-links a, ul.nav-links a:focus {
		font-size: 18px;
		padding: 10px 20px 15px 20px;
	}

	.page-section-home-gallery {
		padding: 70px 0 0 0;
	}

	.page-section-home-white:before {
		height: 158px;
	}

	.page-section-home-white:after {
		height: 158px;
	}

	.page-section-home-white {
		margin-top: 200px;
		margin-bottom: 200px;
		padding-top: 40px;
		padding-bottom: 40px;
	}

	.rate-tmb-header {
		min-height: 170px;
	}

	.rate-tmb-obj {
		transform: scale(.5);
		transform-origin: right bottom;
		right: 10px !important;
		bottom: 10px !important;
	}

	.rate-tmb-header-logo {
		left: 10px;
		top: 10px;
	}

	.rate-tmb-descr {
		padding-left: 0;
		padding-right: 0;
	}

	.footer-obj-1,
	.footer-obj-2 {
		display: none;
	}



}


// md END

