.form-col-narrow {
	flex: 0 0 50%;
	max-width: 50%;
}

.form-col-wide {
	flex: 0 0 100%;
	max-width: 100%;
}

.form-group {
	position: relative;
	margin: 0 0 30px 0;
	padding: 0 0 0 0;
}

label.placeholder {
	font-family: 'Gilroy';
	font-size: 20px;
	line-height: 1.5;
	font-weight: normal;
	color: #939393;
	display: block;
	position: absolute;
	top: 0px;
	transition: all .3s ease;
	margin: 0;
	border: 1px solid transparent;
	padding: 15px 34px 13px 34px;
}

.card-form label.placeholder {
	font-size: 26px;
	font-weight: 500;
	color: #989898;
	padding: 12px 20px 9px 20px;
	top: 11px;
}

label.placeholder.active {
	font-size: 12px;
	line-height: 14px;
	top: 0;
	padding: 0 35px 0 35px;
	border-width: 0;
}

.card-form label.placeholder {
	padding: 0 20px;
}

.footer-subscribe-form label.placeholder {
	padding: 14px 0 0 40px;
	border-left: 2px solid transparent;
}

.footer-subscribe-form label.placeholder:not(.active) {
	color: #fff;
	font-size: 24px;
}

.footer-subscribe-form label.placeholder.active {
	color: #fff;
	padding-top: 0;
	padding-left: 40px;
}

label.error {
	position: absolute;
	font-size: 14px;
	line-height: 18px;
	font-weight: 500;
	color: $err-color;
	margin: 1px 0 0 0;
}

.footer-subscribe-form label.error {
	margin: 1px 0 0 42px;
}

input[type=text],
input[type=email],
input[type=password],
textarea {
	border: 1px solid #F5F5F5;
	padding: 15px 34px 13px 34px;
	background: #F5F5F5;
	transition: all .2s ease;
	font-family: 'Gilroy';
	font-size: 20px;
	line-height: 1.5;
	font-weight: bold;
	color: #000;
	width: 100%;
	display: block;
	outline: none !important;
}

.form-alt input[type=text],
.form-alt input[type=email],
.form-alt input[type=password],
.form-alt textarea {
	background-color: #fff;
	border-color: #fff;
}

.footer-subscribe-form input[type=text],
.footer-subscribe-form input[type=email],
.footer-subscribe-form input[type=password],
.footer-subscribe-form textarea {
	background: transparent;
	border: none;
	border-left: 2px solid #fff;
	color: #fff;
	font-size: 24px;
	padding: 14px 0 0 40px;
}

.card-form input[type=text],
.card-form input[type=email],
.card-form input[type=password],
.card-form textarea {
	border: none;
	background: none;
	font-size: 26px;
	padding: 12px 20px 9px 20px;
}

textarea {
	resize: none;
}

input[type=text].error,
input[type=email].error,
input[type=password].error,
textarea.error {
	border-color: $err-color;
	color: $err-color;
}

.team-form .form-button .btn {
	width: 100%;
}

.order-form .form-footer .btn {
	width: 100%;
}

.feedback-form .form-footer .btn {
	min-width: 285px;
}

.form-col-name {
	flex: 0 0 58.4%;
	max-width: 58.4%;
}

.form-col-phone {
	flex: 0 0 41.6%;
	max-width: 41.6%;
}

.form-agree {
	font-size: 14px;
	line-height: 1.3;
}

.order-form .form-agree {
	max-width: 360px;
}

input[type=checkbox] {
	position: absolute;
	visibility: hidden;
}

input[type=checkbox] + label {
	position: relative;
	padding: 0 0 0 45px;
	font-size: 20px;
	line-height: 1.5;
	font-weight: 500;
	position: relative;
}

input[type=checkbox] + label:before {
	content: "";
	width: 25px;
	height: 25px;
	position: absolute;
	left: 0;
	top: 2px;
	background-color: #F5F5F5;
	transition: all .1s ease;
	background-image: url(../images/check.svg);
	background-repeat: no-repeat;
	background-position: center center;
	background-size: 0 0;
}

input[type=checkbox]:checked + label:before {
	background-color: #00D2FD;
	background-size: 13px 10px;
}

.form-checkboxes-small input[type=checkbox] + label {
	font-size: 14px;
}

.form-checkboxes-small input[type=checkbox] + label:before {
	top: -2px;
}

.form-checkboxes-small {
	margin-bottom: 25px;
}

.form-footer {
	margin: 0;
}

.form-footer .btn {
	width: 100%;
}

.form-footer .row {
	align-items: center;
}

.team-form-col {
	flex: 0 0 33.3333%;
	max-width: 33.3333%;
}

.team-form .form-button {
	margin: 20px 0 0 0;
}

.team-form .form-agree {
	font-weight: 500;
	font-size: 16px;
	line-height: 130%;
	color: #fff;
	margin: 20px 0 0 0;
}

.form-file {
	text-align: center;
	margin: 10px 0 35px 0;
}

.team-form .form-file {
	margin-top: 20px;
}

.form-file .file-caption {
	-ms-flex-order: 2 !important;
	order: 2 !important;
	width: auto !important;
	height: auto !important;
	padding: 0 !important;
	margin: 0 !important;
	border: none !important;
	outline: none !important;
	display: none;
	background: none !important;
	border-radius: 0 !important;
	-ms-flex-preferred-size: 100%;
	flex-basis: 100%;
	box-shadow: none !important;
}

.file-caption.icon-visible {
	display: block;
	overflow: hidden;
}

.form-file .file-caption-name {
	font-weight: 500;
	font-size: 20px;
	line-height: 120%;
	color: #00D2FD;
	text-align: left !important;
	padding: 0 !important;
	margin: 35px 0 0 41px;
}

.form-file .input-group-append {
	-ms-flex-pack: center !important;
	justify-content: center !important;
	height: 22px !important;
}

.form-file .btn-file,
.form-file .btn-file:focus {
	font-weight: normal;
	text-transform: uppercase;
	font-size: 0 !important;
	line-height: 37px !important;
	height: 37px !important;
	/* or 260% */
	text-align: center;
	color: $blue-color !important;
	padding: 6px 0 0 41px !important;
	background: none !important;
	border-radius: 0 !important;
	cursor: pointer;
	display: block;
	position: relative;
	transition: all .15s ease;
	box-shadow: none !important;
}

.form-file .btn-file span {
	font-weight: bold;
	font-size: 20px;
	line-height: 1.5;
	text-transform: uppercase;
}

.form-file .btn-file:hover {
	color: #D7DC43 !important;
}

.form-file .btn-file:before {
	content: "";
	width: 34px;
	height: 37px;
	background: url(../images/file-ico.svg) 0 0 no-repeat;
	background-size: 100% auto;
	position: absolute;
	left: 0;
	top: 0;
	transition: all .15s ease;
	pointer-events: none;
}

.form-file .btn-file:after {
	content: "";
	width: 34px;
	height: 37px;
	background: url(../images/file-ico-hover.svg) 0 0 no-repeat;
	background-size: 100% auto;
	position: absolute;
	left: 0;
	top: 0;
	transition: all .15s ease;
	opacity: 0;
	pointer-events: none;
}

.form-file .btn-file:hover:before {
	opacity: 0;
}

.form-file .btn-file:hover:after {
	opacity: 1;
}

.fileinput-remove {
	display: none !important;
	position: absolute !important;
	top: 100%;
	left: 3px;
	padding: 0 !important;
	margin: 35px 0 0 0 !important;
	font-size: 0 !important;
	line-height: 0 !important;
	width: 22px !important;
	height: 22px !important;
	box-shadow: none !important;
	background-color: transparent !important;
}

.fileinput-remove:before {
	content: "";
	position: absolute;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	background: url(../images/ico-remove.svg) center center no-repeat;
	transition: all .2s ease;
	opacity: 1;
}

.fileinput-remove:after {
	content: "";
	position: absolute;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	background: url(../images/ico-remove-hover.svg) center center no-repeat;
	transition: all .2s ease;
	opacity: 0;
}

.fileinput-remove:hover:before {
	opacity: 0;
}

.fileinput-remove:hover:after {
	opacity: 1;
}

.fileinput-remove i {
	display: none !important;
}

.form-file .input-group-btn {
	position: relative;
	font-size: 14px !important;
	line-height: 22px !important;
	margin: 0 !important;
	outline: none !important;
}

.file-caption.icon-visible + .input-group-btn .fileinput-remove {
	display: block !important;
}

.file-caption + .input-group-btn {
	display: block !important;
}

.btn-file i {
	display: none !important;
}

.btn-file .kv-upload-progress {
	display: none !important;
}

.form-group-file.form-group-error:after {
	content: " ";
	font-size: 11px;
	line-height: 15px;
	text-align: right;
	margin: 13px 0 0 0;
	color: #FF0006;
	position: absolute;
	top: 100%;
	left: 0;
	right: 0;
}

.form-group-file label.error {
	display: none !important;
}

.footer-subscribe-form {
	display: flex;
	flex-wrap: nowrap;
}

.footer-subscribe-field {
	flex-grow: 1;
	margin: 0 30px 0 0;
}

.footer-subscribe-form input[type=submit] {
	background: transparent url(../images/footer-subscribe-submit.svg) 0 0 no-repeat;
	width: 50px;
	height: 51px;
	border: none;
	-webkit-appearance: none;
	font-size: 0;
	line-height: 0;
	color: transparent;
	display: block;
	cursor: pointer;
	margin-top: -1px;
}

.footer-subscribe-form .form-group {
	margin: 0;
}

.card-form {
	display: flex;
	background: #fff;
	border-radius: 40px;
	padding: 10px;
}

.card-form .form-group {
	margin: 0;
}

.card-field {
	flex-grow: 1;
}

.card-button {
	flex-grow: 0;
}

.card-button .btn {
	min-width: 255px;
}

.card-form-agree {
	font-size: 14px;
	line-height: 1.22;
}

.card-form-agree a,
.card-form-agree a:focus {
	text-decoration: underline;
	color: #000;
}

.contacts-form .form-agree {
	color: #000;
}

.contacts-form .form-agree a,
.contacts-form .form-agree a:focus {
	color: #000;
}

.contacts-form-footer-col-1 {
	flex-grow: 1;
	margin-left: 16.6666%;
}

.contacts-form-footer-col-2 {
	flex: 0 0 380px;
	max-width: 380px;
}

input[type=submit] {
	outline: none !important;
}